import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  isAdmin: false,
};

const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    setAdmin: state => {
      state.isAdmin = true;
    },
    removeAdmin: state => {
      state.isAdmin = false;
    },
  },
});

export const { setAdmin, removeAdmin } = authSlice.actions;
export const selectAdmin = state => state.auth.isAdmin;
export default authSlice.reducer;
