import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  projectList: [],
};

const projectsSlice = createSlice({
  name: 'projects',
  initialState,
  reducers: {
    // It will receive an object with a new input
    setProjectList: (state, action) => {
      state.projectList.push(action.payload);
    },

    overrideProjectList: (state, action) => {
      state.projectList = action.payload;
    },

    deleteProject: (state, action) => {
      var id = action.payload;
      state.projectList = state.projectList.filter((project) => {
        return project.id !== id;
      })
    },

    toggleHidden: (state, action) => {
      var id = action.payload;
      state.projectList.map(project => {
        if (project.id === id) {
          project.hidden = !project.hidden;
        }
      });
    },

    //
    toggleProject: (state, action) => {
      var id = action.payload;
      console.log(id);
      state.projectList.map(project => {
        if (project.id === id) {
          if (project.project === 'programming') {
            project.project = 'editing';
          } else {
            project.project = 'programming';
          }
        }
      });
    },

    toggleFeatured: (state, action) => {
      var id = action.payload;
      state.projectList.map(project => {
        if (project.id === id) {
          project.featured = !project.featured;
        }
      });
    },
  },
});

export const { setProjectList, toggleFeatured, toggleHidden, toggleProject, deleteProject, overrideProjectList } =
  projectsSlice.actions;
export const selectProject = state => state.projects.projectList;
export default projectsSlice.reducer;
