import React, { useState } from 'react';
import styled from 'styled-components';
import {
  Badge,
  Button,
  Checkbox,
  Radio,
  RadioGroup,
  AlertDialog,
  AlertDialogBody,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogContent,
  AlertDialogOverlay,
  useToast,
} from '@chakra-ui/react';
import { useDispatch } from 'react-redux';
import {
  toggleFeatured,
  toggleHidden,
  toggleProject,
  deleteProject,
} from '../features/projectsSlice';
import { useHistory } from 'react-router-dom';
import MenuIcon from '@material-ui/icons/Menu';
import {
  useFirestoreDocData,
  useFirestore,
  useFirestoreCollectionData,
} from 'reactfire';
import 'firebase/firestore';
// eslint-disable-next-line react/prop-types
const ProjectArticle = ({
  id,
  title,
  featured,
  project,
  hidden,
  isDragging,
  provided,
}) => {
  const [isDeleteOpen, setIsDeleteOpen] = useState(false);
  const cancelRef = React.useRef();
  const toast = useToast();
  const dispatch = useDispatch();
  const onClose = () => setIsDeleteOpen(false);

  const arrayDataRef = useFirestore().collection('projects').doc('1');
  const dataRef = useFirestore().collection('data').doc(`${id}`);

  const arrayData = useFirestoreDocData(arrayDataRef);

  const deleteItem = () => {
    toast({
      title: 'Project Deleted',
      description: 'The project has been deleted!',
      status: 'success',
      duration: 3000,
      isClosable: true,
    });
    dataRef.delete();
    // AHora falta filtrar todo el project data
    var newArrayData = arrayData.data.projectData.filter(individualData => {
      return individualData.id != id;
    });

    arrayDataRef.set({ projectData: newArrayData });
    dispatch(deleteProject(id));
    // Borrarlo de la base de datos

    onClose();
  };

  // Cuando el proyecto se cree, que tambien cree una pestaña de detail y asigne el titulo

  const handleFeatured = () => {
    dispatch(toggleFeatured(id));
  };
  const handleProject = () => {
    dispatch(toggleProject(id));
  };
  const handleHidden = () => {
    dispatch(toggleHidden(id));
  };

  const history = useHistory();

  function editItem() {
    history.push(`/edit/${id}`);
    window.location.reload();
  }

  return (
    <Container isDragging={isDragging}>
      <MainContent>
        <div {...provided.dragHandleProps}>
          <Burger />
        </div>
        <h1> {id} </h1>
        <Title> {title}</Title>
        {featured && <Badge colorScheme="yellow"> Featured </Badge>}
        {hidden && <Badge colorScheme="red"> Hidden </Badge>}
        {project === 'programming' ? (
          <Badge colorScheme="blue"> programming </Badge>
        ) : (
          <Badge colorScheme="green"> Video Editing </Badge>
        )}
      </MainContent>
      <Form>
        <SRadioGroup onChange={() => handleProject()} value={project} size="sm">
          <SRadio value="programming"> P</SRadio>
          <SRadio value="editing"> V </SRadio>
        </SRadioGroup>
        <FeaturedCheckbox
          isChecked={featured ? true : false}
          onChange={() => handleFeatured()}
          size="sm"
        >
          Featured
        </FeaturedCheckbox>

        <FeaturedCheckbox
          isChecked={hidden ? true : false}
          onChange={() => handleHidden()}
          size="sm"
        >
          Hidden
        </FeaturedCheckbox>
        <EditButton colorScheme="blue" size="sm" onClick={() => editItem()}>
          {' '}
          Edit{' '}
        </EditButton>
        <DeleteButton
          colorScheme="red"
          size="sm"
          onClick={() => setIsDeleteOpen(true)}
        >
          Delete
        </DeleteButton>
        <AlertDialog
          isOpen={isDeleteOpen}
          leastDestructiveRed={cancelRef}
          onClose={onClose}
        >
          <AlertDialogOverlay>
            <AlertDialogContent style={{ color: 'black' }}>
              <AlertDialogHeader fontSize="lg" fontWeight="bold">
                Delete Project
              </AlertDialogHeader>
              <AlertDialogBody>
                Are you sure you want to delete this project?
              </AlertDialogBody>
              <AlertDialogFooter>
                <Button ref={cancelRef} onClick={onClose}>
                  Cancel
                </Button>
                <Button colorScheme="red" onClick={() => deleteItem()} ml={3}>
                  Delete
                </Button>
              </AlertDialogFooter>
            </AlertDialogContent>
          </AlertDialogOverlay>
        </AlertDialog>
      </Form>
    </Container>
  );
};

const Container = styled.div`
  background: ${props => (props.isDragging ? 'rgb(244,244,244)' : 'white')};
  border: 1px solid rgba(128, 128, 128, 0.3);
  border-radius: 5px;
  display: flex;
  align-items: center;
  width: 1100px;
  padding: 10px 0;
  span {
    margin: 0 10px;
  }
`;

const Burger = styled(MenuIcon)`
  margin: 0 20px;
`;
const MainContent = styled.div`
  min-width: 450px;
  display: flex;
  align-items: center;
`;

const Form = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
`;
const Title = styled.h1`
  font-size: 15px;
  font-weight: bold;
  margin-right: 10px;
`;

const EditButton = styled(Button)`
  margin: 0 20px;
`;
const FeaturedCheckbox = styled(Checkbox)`
  margin-left: 20px;
`;

const SRadioGroup = styled(RadioGroup)`
  margin-left: auto;
  font-size: 15px;
`;

const SRadio = styled(Radio)`
  font-size: 5px;
`;

const DeleteButton = styled(Button)`
  margin-right: 20px;
`;

export default ProjectArticle;
