import React, { useState, useEffect } from 'react';
import ProjectArticle from './ProjectArticle';
import styled from 'styled-components';
import { useHistory } from 'react-router-dom';

import { Flex, Spacer, Button, Stack, useToast } from '@chakra-ui/react';
import { useDispatch, useSelector } from 'react-redux';
import { selectProject, overrideProjectList } from '../features/projectsSlice';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import ProjectInput from './ProjectInput';

import { useFirestoreDocData, useFirestore } from 'reactfire';
import 'firebase/firestore';

import { selectAdmin, setAdmin, removeAdmin } from '../features/authSlice';

const Dashboard = () => {
  const history = useHistory();

  useEffect(() => {
    if (
      localStorage.getItem('username') !== process.env.REACT_APP_USERNAME ||
      localStorage.getItem('password') !== process.env.REACT_APP_PASSWORD
    ) {
      history.push('login/');
    }
  }, []);

  const [projectInput, setProjectInput] = useState(false);

  const dispatch = useDispatch();
  const projects = useSelector(selectProject);

  const toggleProjectInput = () => {
    setProjectInput(!projectInput);
  };

  const onDragEnd = event => {
    // console.log(event.destination.index + ' ' + event.source.index);

    if (event.destination == null || event.source == null) {
      return;
    }
    var destination = event.destination.index,
      source = event.source.index;

    var startSlice = projects.slice(0, Math.min(destination, source));
    var endSlice = projects.slice(
      Math.max(destination, source) + 1,
      projects.length
    );

    var editedArray = [];
    if (destination > source) {
      let changedSlice = projects.slice(source + 1, destination + 1);
      editedArray = [
        ...startSlice,
        ...changedSlice,
        projects[source],
        ...endSlice,
      ];
    } else {
      let changedSlice = projects.slice(destination, source);
      editedArray = [
        ...startSlice,
        projects[source],
        ...changedSlice,
        ...endSlice,
      ];
    }

    dispatch(overrideProjectList(editedArray));
  };

  const dataRef = useFirestore().collection('projects').doc('1');
  const { status, data } = useFirestoreDocData(dataRef);

  useEffect(() => {
    if (data) {
      dispatch(overrideProjectList(data.projectData));
      console.log(data.projectData);
    } else {
      dispatch(overrideProjectList([]));
    }
  }, [data]);

  const toast = useToast();
  function saveProjects() {
    toast({
      title: 'Projects saved',
      description: 'Projects has been saved successfully',
      status: 'success',
    });
    dataRef.set({
      projectData: projects,
    });
  }

  function handleLogout() {
    localStorage.setItem('username', '');
    localStorage.setItem('password', '');
    history.push('login/');
  }
  return (
    <DashboardContainer>
      <Wrapper>
        <Flex align="center" p={5}>
          <Title>
            <span>Project Dashboard</span>
          </Title>
          <Spacer />
          <Stack direction="row">
            <Button colorScheme="orange" size="sm" onClick={toggleProjectInput}>
              {projectInput ? <span>Close</span> : <span>Add Project </span>}
            </Button>
            <Button colorScheme="green" size="sm" onClick={saveProjects}>
              Save Projects!
            </Button>
            <Button
              colorScheme={'red'}
              size={'sm'}
              onClick={() => handleLogout()}
            >
              Log out
            </Button>
          </Stack>
        </Flex>

        {projectInput && <ProjectInput />}
      </Wrapper>
      {status == 'loading' ? (
        <h1> Loading </h1>
      ) : (
        <DragDropContext onDragEnd={onDragEnd}>
          <Droppable droppableId="hola">
            {provided => (
              <Articles ref={provided.innerRef} {...provided.droppableProps}>
                {projects.length > 0 ? (
                  projects.map((projectCard, index) => {
                    return (
                      <Draggable
                        draggableId={'d' + projectCard.id}
                        index={index}
                        key={projectCard.id}
                      >
                        {(provided, snapshot) => (
                          <DraggableDiv
                            {...provided.draggableProps}
                            ref={provided.innerRef}
                          >
                            <ProjectArticle
                              id={projectCard.id}
                              title={projectCard.name}
                              featured={projectCard.featured}
                              project={projectCard.project}
                              hidden={projectCard.hidden}
                              isDragging={snapshot.isDragging}
                              provided={provided}
                            />
                          </DraggableDiv>
                        )}
                      </Draggable>
                    );
                  })
                ) : (
                  <p> Nothing to show!</p>
                )}
                {provided.placeholder}
              </Articles>
            )}
          </Droppable>
        </DragDropContext>
      )}
    </DashboardContainer>
  );
};

const DashboardContainer = styled.div`
  color: black;
  background-color: white;
  height: 1000vh;
`;
const DraggableDiv = styled.div`
  margin-bottom: 10px;
`;
const Title = styled.h1`
  span {
    font-size: 30px;
    font-weight: bold;
  }
`;

const Articles = styled.div`
  margin: auto;
  border-radius: 4px;
  padding: 30px 15px;
  width: 90%;
  border: 2px solid rgba(0, 0, 0, 0.1);
`;
const Wrapper = styled.div`
  width: 80%;
  margin: auto;
`;
export default Dashboard;
