import React from 'react';
import { ChakraProvider, extendTheme } from '@chakra-ui/react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import '@fontsource/montserrat';
import Navbar from './components/Navbar';
import ProgrammingPortfolio from './components/ProgrammingPortfolio';
import ProgrammingDetail from './components/ProgrammingDetail';
import { AnimatePresence } from 'framer-motion';
import Dashboard from './components/Dashboard';
import Edit from './components/Edit';
import Content from 'components/Content';
import Footer from 'components/footer';
import { mode } from '@chakra-ui/theme-tools';
import Login from './components/Login';

const components = {
  Drawer: {
    // setup light/dark mode component defaults
    baseStyle: props => ({
      dialog: {
        bg: mode('#211f1f', '#211f1f')(props),
      },
    }),
  },
};

const theme = extendTheme({
  fonts: {
    body: 'Montserrat',
  },
  styles: {
    global: {
      body: {
        color: 'white',
        bg: 'black',
      },
    },
  },
  components,
});

function App() {
  return (
    <ChakraProvider theme={theme}>
      <Router>
        <Switch>
          <Route exact path="/dashboard">
            <Dashboard />
          </Route>
          <Route path="/edit/:pageId" component={Edit} />
          <Route path="/login" component={Login} />
          <Route>
            <DefaultContainer />
          </Route>
          <Route exact path="*" key="5">
            <h1> Error 404</h1>
          </Route>
        </Switch>
      </Router>
    </ChakraProvider>
  );
}

function DefaultContainer() {
  return (
    <>
      <AnimatePresence>
        <Navbar key="1" />
        <Route path="/programming" key="2">
          <ProgrammingPortfolio />
        </Route>
        <Route path="/programmingdetail/:pageId" key="3">
          <ProgrammingDetail />
        </Route>
        <Route exact path="/" key="4">
          <Content />
        </Route>
        <Footer />
      </AnimatePresence>
    </>
  );
}

export default App;
