import React, { useEffect, useLayoutEffect, useState } from 'react';
import styled from 'styled-components';
import Carousel from './Carousel';
import PortfolioButtons from './PortfolioButtons';
import { motion } from 'framer-motion';
import StarsIcon from '@material-ui/icons/Stars';
import { useFirestoreDocData, useFirestore } from 'reactfire';
import 'firebase/firestore';
import 'firebase/storage';
import { useStorage, useStorageDownloadURL } from 'reactfire';
import Typewriter from 'typewriter-effect';
import { HStack, Box } from '@chakra-ui/react';
import { useMediaPredicate } from 'react-media-hook';

const Content = () => {
  // Aqui tengo que fetchear todos los projectos y buscar los featureds y obtener sus imagenes.§

  const [featuredData, setFeaturedData] = useState([]);

  const dataRef = useFirestore().collection('projects').doc('1');
  const { status, data } = useFirestoreDocData(dataRef);
  const storage = useStorage();
  const biggerThan400 = useMediaPredicate('(min-width: 400px)');
  useEffect(() => {
    if (status !== 'loading') {
      var filteredArray = data.projectData.filter(project => project.featured);

      var featuredImagesArray = [];

      filteredArray.forEach(item => {
        const storageRef = storage
          .ref(`${item.id}/`)
          .listAll()
          .then(items => {
            var imageRef = items.items[0];
            imageRef.getDownloadURL().then(url => {
              setFeaturedData(currentData => [
                ...currentData,
                {
                  name: item.name,
                  id: item.id,
                  image: url,
                },
              ]);
            });
          });
      });
    }
    console.log(featuredData);
  }, [data]);

  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  });

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <Container>
        <Box ml={10} mt={10}>
          <Title biggerThan400={biggerThan400}>
            {' '}
            Hello, I'm <strong> Mauricio Muñoz</strong>
          </Title>
          <Box mt={2}>
            <HStack>
              <p>I'm a</p>
              <Typewriter
                options={{
                  strings: ['developer 💻', 'video editor 📹', 'designer 🎯'],
                  autoStart: true,
                  loop: true,
                }}
                onInit={typewriter => {
                  console.log('init');
                }}
              />
            </HStack>
          </Box>
        </Box>

        <Carousel featured={featuredData} />

        <Subtitle>
          <StarsIcon />
          <span>Highlighted Projects </span>
          <StarsIcon />
        </Subtitle>
        <PortfolioButtons />
      </Container>
    </motion.div>
  );
};

const Container = styled.div`
  min-height: 100vh;
  position: relative;
  z-index: 1;
  overflow-x: hidden;
  padding-bottom: 20px;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    background-image: url('./imgs/bg.jpeg');
    opacity: 40%;
  }
`;
const Subtitle = styled.div`
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
  span {
    margin: 0 20px;
  }
`;

const Title = styled.h1`
  font-size: 30px;

  strong {
    font-window: bold;
    display: ${props => (props.biggerThan400 ? 'inline' : 'block')};
  }
`;
export default Content;
