import React from 'react';
import { useForm } from 'react-hook-form';
import { Input, FormControl, FormLabel, Button } from '@chakra-ui/react';
import styled from 'styled-components';
import { setAdmin } from '../features/authSlice';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';

const Login = () => {
  const dispatch = useDispatch();

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const history = useHistory();
  function validateLogin(data) {
    console.log(data);
    if (
      data.username === process.env.REACT_APP_USERNAME &&
      data.password === process.env.REACT_APP_PASSWORD
    ) {
      localStorage.setItem('username', data.username);
      localStorage.setItem('password', data.password);
      history.push('/dashboard');
    }
  }
  return (
    <Wrapper>
      <form onSubmit={handleSubmit(validateLogin)}>
        <FormControl isRequired>
          <FormLabel> Username </FormLabel>
          <Input {...register('username')} />
        </FormControl>
        <FormControl isRequired mt={5}>
          <FormLabel> Password </FormLabel>
          <Input type="password" {...register('password')} />
        </FormControl>
        <Button type={'submit'} colorScheme={'whiteAlpha'} mt={5} ml={'auto'}>
          {' '}
          Submit
        </Button>
      </form>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 80%;
  margin: auto;
  margin-top: 30vh;
`;
export default Login;
