import React, { useLayoutEffect, useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import { Button, Stack } from '@chakra-ui/react';
import GitHubIcon from '@material-ui/icons/GitHub';
import LanguageIcon from '@material-ui/icons/Language';
import { motion } from 'framer-motion';
import { useHistory } from 'react-router-dom';
import Carousel from './Carousel';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import { useFirestoreDocData, useFirestore } from 'reactfire';
import { useParams } from 'react-router-dom';
import 'firebase/firestore';
import 'firebase/storage';
import { useStorage, useStorageDownloadURL } from 'reactfire';
import { Link } from 'react-router-dom';
const ProgrammingDetail = () => {
  const [Github, setGithub] = useState('');
  const [Webpage, setWebpage] = useState('');
  const [TitleText, setTitleText] = useState('');
  const [DescriptionText, setDescriptionText] = useState('');
  const [images, setImages] = useState([]);

  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  });

  let history = useHistory();
  const storage = useStorage();

  const { pageId } = useParams();
  const dataRef = useFirestore().collection('data').doc(pageId);
  const { status, data } = useFirestoreDocData(dataRef);

  useEffect(() => {
    const storageRef = storage
      .ref(`${pageId}/`)
      .listAll()
      .then(items =>
        items.items.forEach(element => {
          console.log(element);
          saveLink(element);
        })
      );
  }, []);

  function saveLink(imageRef) {
    imageRef.getDownloadURL().then(url => {
      setImages(currImg => [
        ...currImg,
        {
          name: '',
          image: url,
          id: -1,
        },
      ]);
      console.log(images);
    });
  }

  const goBack = () => {
    history.goBack();
  };

  useEffect(() => {
    if (data) {
      setTitleText(data.title);
      setDescriptionText(data.description);
      setWebpage(data.website);
      setGithub(data.github);
    }
  }, [data]);

  // console.log(imageData);

  // useEffect(() => {
  //   console.log('1' + imageData);
  // }, [imageData]);

  return (
    <ContainerBg>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <Container>
          <Header>
            <Title> {TitleText}</Title>
            <GoBackButton
              rightIcon={<KeyboardBackspaceIcon />}
              variant="ghost"
              onClick={goBack}
            >
              {' '}
              Go back{' '}
            </GoBackButton>
          </Header>
          {images.length > 0 && (
            <Carousel featured={images} shouldClick={false} />
          )}
          <Images></Images>
          <Description>{DescriptionText}</Description>
          <Stack align="center" mt={10} direction={['column', 'row']}>
            {Github !== '' && (
              <a href={Github} target="_blank" rel="noopener noreferrer">
                <GithubButton
                  rightIcon={<GitHubIcon />}
                  variant="outline"
                  onClick={() => {
                    console.log(Github);
                  }}
                >
                  {' '}
                  Code on Github
                </GithubButton>
              </a>
            )}
            {Webpage !== '' && (
              <a href={Webpage} target="_blank" rel="noopener noreferrer">
                <WebsiteButton rightIcon={<LanguageIcon />}>
                  {' '}
                  Check out the Website
                </WebsiteButton>
              </a>
            )}
          </Stack>
        </Container>
      </motion.div>
    </ContainerBg>
  );
};

const ContainerBg = styled.div`
  position: relative;
  z-index: 1;
  overflow-x: hidden;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    background-image: url('./imgs/bg.jpeg');
    opacity: 40%;
  }
`;
const Container = styled.div`
  width: 90%;
  margin: auto;
  margin-bottom: 50px;
`;
const Header = styled.div`
  margin-top: 15px;
  display: flex;
`;
const Title = styled.h1`
  font-size: 30px;
  font-weight: bold;
  margin-right: auto;
`;

const Images = styled.div``;
const Description = styled.p`
  margin-top: 20px;
  font-size: 16px;
`;

const GithubButton = styled(Button)`
  &:hover {
    color: black;
  }
`;

const WebsiteButton = styled(Button)`
  width: 200px;
  color: black;
`;

const GoBackButton = styled(Button)`
  &:hover {
    background: transparent !important;
    transform: translateX(10px);
  }
`;
export default ProgrammingDetail;
