import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import { useMediaPredicate } from 'react-media-hook';
import { useHistory } from 'react-router-dom';
import { useFirestoreDocData, useFirestore } from 'reactfire';
import 'firebase/firestore';
import 'firebase/storage';
import { useStorage, useStorageDownloadURL } from 'reactfire';

const ProgrammingProjects = () => {
  const biggerThan900 = useMediaPredicate('(min-width: 900px)');
  const biggerThan450 = useMediaPredicate('(min-width: 450px)');
  const [projectInfo, setProjectInfo] = useState([]);
  let history = useHistory();

  const handleClick = targetId => {
    history.push(`/programmingdetail/${targetId}`);
  };

  const dataRef = useFirestore().collection('projects').doc('1');
  const { status, data } = useFirestoreDocData(dataRef);
  const storage = useStorage();

  const divHeight = useRef();
  const [currentDivHeight, setCurrentDivHeight] = useState(0);

  useEffect(() => {
    if (status !== 'loading') {
      var filteredArray = data.projectData.filter(
        project => project.project == 'programming'
      );
      var projectList = [];
      console.log(filteredArray);
      filteredArray.forEach(item => {
        const storageRef = storage
          .ref(`${item.id}/`)
          .listAll()
          .then(items => {
            var imageRef = items.items[0];
            imageRef.getDownloadURL().then(url => {
              setProjectInfo(currentData => [
                ...currentData,
                {
                  name: item.name,
                  id: item.id,
                  image: url,
                },
              ]);
              // projectList.push(url);
              // console.log(projectList);
              // setProjectInfo(
              //   filteredArray.map((item, index) => {
              //     return {
              //       name: item.name,
              //       image: projectList[index],
              //       id: item.id,
              //     };
              //   })
              // );
            });
          });
      });
    }
    console.log(projectInfo);
  }, [data]);

  useEffect(() => {
    console.log(divHeight);
    setCurrentDivHeight(divHeight.current.scrollHeight);
    console.log(currentDivHeight);
  }, [divHeight.current]);

  return (
    <MainWrapper height={currentDivHeight + 800}>
      <Title> Programming Projects </Title>
      <Container
        ref={divHeight}
        columns={biggerThan900 ? 3 : biggerThan450 ? 2 : 1}
      >
        {projectInfo.map(project => (
          <Wrapper onClick={() => handleClick(project.id)}>
            <span>{project.name}</span>
            <img src={project.image} alt="" />
          </Wrapper>
        ))}
      </Container>
    </MainWrapper>
  );
};

const MainWrapper = styled.div`
  height: ${props => props.height + 'px'};
`;

const Container = styled.div`
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(${props => props.columns}, minmax(0, 1fr));
  grid-gap: 25px;
  height: 200px;
  margin-bottom: 50px;
`;

const Title = styled.h1`
  margin-top: 40px;
  margin-bottom: 40px;
  text-align: center;
  font-size: 20px;
`;

const Wrapper = styled.div`
  height: 200px;
  transition: all 0.3s;
  box-shadow: 0px 10px 43px 11px rgba(0, 0, 0, 0.3);
  position: relative;
  cursor: pointer;

  img {
    border-radius: 8px;
    border: 4px solid rgba(255, 255, 255, 0.3);
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  span {
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    font-size: 20px;
    font-weight: bold;
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: black;
    opacity: 0.3;
  }
  &:hover {
    transform: scale(1.05);
    img {
      border: 4px solid rgba(255, 255, 255, 1);
    }

    :before {
      opacity: 0.1;
    }
  }
`;
export default ProgrammingProjects;
