import React from 'react';
import styled from 'styled-components';
import { Tooltip } from '@chakra-ui/react';
import { useMediaPredicate } from 'react-media-hook';

const ProgrammingIcons = () => {
  const biggerThan900 = useMediaPredicate('(min-width: 900px)');
  const biggerThan450 = useMediaPredicate('(min-width: 450px)');

  return (
    <Icons rows={biggerThan900 ? 6 : biggerThan450 ? 3 : 2}>
      <Tooltip label="Javascript" placement="top">
        <IconWrapper>
          <img
            src="http://1000marcas.net/wp-content/uploads/2020/11/JavaScript-logo-600x375.png"
            alt=""
          />
        </IconWrapper>
      </Tooltip>
      <Tooltip label="React" placement="top">
        <IconWrapper>
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/4/47/React.svg/1200px-React.svg.png"
            alt=""
          />
        </IconWrapper>
      </Tooltip>
      <Tooltip label="Node.js" placement="top">
        <IconWrapper>
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/d/d9/Node.js_logo.svg/1280px-Node.js_logo.svg.png"
            alt=""
          />
        </IconWrapper>
      </Tooltip>
      <Tooltip label="Python" placement="top">
        <IconWrapper>
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/c/c3/Python-logo-notext.svg/768px-Python-logo-notext.svg.png"
            alt=""
          />
        </IconWrapper>
      </Tooltip>
      <Tooltip label="Java" placement="top">
        <IconWrapper>
          <img
            src="https://qph.fs.quoracdn.net/main-qimg-c43424186b9c089b9aa1d64c7f1989c1.webp"
            alt=""
          />
        </IconWrapper>
      </Tooltip>
      <Tooltip label="C++" placement="top">
        <IconWrapper>
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/1/18/ISO_C%2B%2B_Logo.svg/1200px-ISO_C%2B%2B_Logo.svg.png"
            alt=""
          />
        </IconWrapper>
      </Tooltip>
    </Icons>
  );
};
const Icons = styled.div`
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(${props => props.rows}, 1fr);
  grid-gap: 20px;
`;

const IconWrapper = styled.div`
  margin: auto;
  width: 128px;
  height: 128px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: all 0.3s;

  &:after {
    content: '';
    border: 3px solid rgba(255, 255, 255, 0.1);
    position: absolute;
    top: -6px;
    bottom: -6px;
    left: -6px;
    right: -6px;
    border-radius: 50%;
  }

  img {
    width: 80%;
    margin: auto;
  }

  &:hover {
    transform: scale(1.05);

    :after {
      border: 3px solid rgba(255, 255, 255, 1);
    }
  }
`;
export default ProgrammingIcons;
