import React, { useCallback, useState, useEffect } from 'react';
import { useDropzone } from 'react-dropzone';
import styled from 'styled-components';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import FileLoading from './FileLoading';
import { Button, useToast } from '@chakra-ui/react';
import ImageIcon from '@material-ui/icons/Image';
import { useStorage, useStorageDownloadURL, useStorageTask } from 'reactfire';
import 'firebase/storage';

export interface UploadableFile {
  file: File;
  errors: FileError[];
}
const Dropzone = ({ pageId }) => {
  const [files, setFiles] = useState([]);

  const onDrop = useCallback((accFiles, rejFiles) => {
    const mappedAcc = accFiles.map(file => ({ file, errors: [] }));
    setFiles(curr => [...curr, ...mappedAcc, ...rejFiles]);
  }, []);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  const deleteFile = id => {
    console.log(id);
    var filteredFiles = files.filter((file, index) => {
      return index !== id;
    });
    setFiles(filteredFiles);
  };

  // Upload file
  const [uploadTask, setUploadTask] = useState(undefined);
  const [ref, setRef] = useState(undefined);
  const storage = useStorage();
  const [isUploading, setIsUploading] = useState(false);
  const [currentFile, setCurrentFile] = useState(1);
  const [images, setImages] = useState([]);

  const uploadFiles = () => {
    setCurrentFile(1);
    files.map((file, index) => {
      console.log(pageId);
      const fileToUpload = file.file;
      const fileName = fileToUpload.name;
      const newRef = storage.ref(pageId).child(fileName);
      setRef(newRef);

      const uploadTask = newRef.put(fileToUpload);

      uploadTask.then(() => {
        console.log('Upload Complete');
        if (index === files.length - 1) {
          setIsUploading(false);
        } else {
          setCurrentFile(currentFile + 1);
        }
        setUploadTask(undefined);
      });
      setUploadTask(uploadTask);
      setIsUploading(true);
    });
  };

  useEffect(() => {
    const storageRef = storage
      .ref(`${pageId}/`)
      .listAll()
      .then(items =>
        items.items.forEach(element => {
          setImages(currImg => [...currImg, element]);
        })
      );
  }, []);

  const toast = useToast();

  function deleteFileFromServer(reference, indexToDelete) {
    reference.delete().then(() => {
      toast({
        title: 'Image Deleted',
        description: 'The image has been deleted',
        status: 'success',
      });
      const filteredImages = images.filter((image, index) => {
        return index !== indexToDelete;
      });

      setImages(filteredImages);
    });
  }

  return (
    <>
      <h1> Images </h1>
      <Zone {...getRootProps()}>
        <input {...getInputProps()} />
        {isDragActive ? (
          <IconWrapper>
            <FileCopyIcon fontSize="large" />
            <DragText> Drop your files here</DragText>
          </IconWrapper>
        ) : (
          <IconWrapper drag>
            <FileCopyIcon fontSize="large" />
            <DragText> Drag and drop some images </DragText>
          </IconWrapper>
        )}
      </Zone>
      {/* {JSON.stringify(files)} */}
      <h1 style={{ marginTop: '30px' }}> Uploaded</h1>
      {images.map((file, index) => {
        return (
          <FilesWrapper>
            <SingleFileWrapper>
              <ImageIcon />
              <p style={{ marginLeft: '10px' }}>{file.name};</p>
              <Button
                colorScheme="red"
                size="sm"
                ml="auto"
                onClick={() => deleteFileFromServer(file, index)}
              >
                {' '}
                Delete{' '}
              </Button>
            </SingleFileWrapper>
          </FilesWrapper>
        );
      })}
      <h1 style={{ marginTop: '30px' }}> To Upload</h1>
      {files.map((file, index) => {
        return (
          <FilesWrapper>
            <SingleFileWrapper>
              <ImageIcon />
              <p style={{ marginLeft: '10px' }}>{file.file.path};</p>
              <Button
                colorScheme="red"
                size="sm"
                ml="auto"
                onClick={() => deleteFile(index)}
              >
                {' '}
                Delete{' '}
              </Button>
            </SingleFileWrapper>
          </FilesWrapper>
        );
      })}
      {isUploading && (
        <UploadingFileText>
          Uploading File {currentFile} of {files.length}
        </UploadingFileText>
      )}
      <UploadButton
        colorScheme="green"
        isDisabled={files.length > 0 ? false : true}
        mt={4}
        onClick={uploadFiles}
      >
        {' '}
        Upload
      </UploadButton>
    </>
  );
};

const Zone = styled.div`
  margin-top: 10px;
  border: 2px dashed rgba(0, 0, 0, 0.1);
  height: 200px;
`;

const IconWrapper = styled.div`
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: all 0.2s;
  color: ${props => (props.drag ? 'rgba(0, 0, 0, 0.5)' : 'rgba(0, 0, 0, 0.9)')};
`;

const DragText = styled.h1`
  margin-top: 10px;
`;

const FilesWrapper = styled.div`
  display: flex;
  align-items: center;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  margin-bottom: 10px;
`;

const SingleFileWrapper = styled.div`
  margin: 10px 0;
  padding: 0 10px;
  width: 100%;
  display: flex;
  align-items: center;
`;

const UploadButton = styled(Button)`
  margin-left: 72vw !important;
`;

const UploadingFileText = styled.h1``;
export default Dropzone;
