import React, { useRef } from 'react';
import styled from 'styled-components';
import {
  Button,
  useDisclosure,
  Input,
  FormControl,
  FormLabel,
  Textarea,
} from '@chakra-ui/react';
import CodeIcon from '@material-ui/icons/Code';
import DesktopMacIcon from '@material-ui/icons/DesktopMac';
import { useMediaPredicate } from 'react-media-hook';
import { Link } from 'react-router-dom';
import {
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useToast,
} from '@chakra-ui/react';
import { useForm } from 'react-hook-form';
import axios from 'axios';

const Navbar = () => {
  const biggerThan700 = useMediaPredicate('(min-width: 700px)');
  const biggerThan330 = useMediaPredicate('(min-width: 330px)');

  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = useRef();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const toast = useToast();

  function sendMessage(data) {
    axios
      .post('https://formsubmit.co/ajax/j.mauricio.munoz1@gmail.com', {
        email: data.email,
        name: data.subject,
        message: data.detail,
      })
      .then(r => {
        toast({
          title: 'Message Sent successfully',
          description: 'Your message has been sent',
          status: 'success',
        });
        onClose();
      });
  }

  return (
    <>
      <NavBar>
        <Wrapper>
          <Logo>
            <Link to="/">
              {biggerThan330 && <span> MM </span>}
              Mauricio Muñoz
            </Link>
          </Logo>
        </Wrapper>
        {biggerThan700 && (
          <Middle>
            <CodeIcon />
            <span> Developer • Video Editor</span>
            <DesktopMacIcon />
          </Middle>
        )}
        <Wrapper>
          <ContactBtn
            colorScheme="white"
            variant="outline"
            _hover={{ bg: 'white', color: 'black' }}
            ref={btnRef}
            onClick={onOpen}
          >
            Contact Me
          </ContactBtn>
        </Wrapper>
      </NavBar>
      <StyledDrawer
        isOpen={isOpen}
        placement="right"
        onClose={onClose}
        finalFocusRef={btnRef}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />
          <DrawerHeader>Contact Me</DrawerHeader>

          <form onSubmit={handleSubmit(sendMessage)}>
            <DrawerBody>
              <FormControl isRequired>
                <FormLabel> Email Adress</FormLabel>
                <Input
                  placeholder="Type your email... "
                  isRequired
                  mb={7}
                  {...register('email', {
                    required: 'Please enter your email',
                  })}
                />
              </FormControl>
              <FormControl isRequired>
                <FormLabel> Subject</FormLabel>
                <Input
                  placeholder="Type your subject..."
                  isRequired
                  mb={7}
                  {...register('subject', {
                    required: 'Please enter your subject',
                  })}
                />
              </FormControl>
              <FormControl isRequired>
                <FormLabel> Detail </FormLabel>
                <Textarea
                  placeholder="Type your request..."
                  isRequired
                  mb={7}
                  {...register('detail', {
                    required: 'Please specify your request',
                  })}
                />
              </FormControl>
            </DrawerBody>

            <DrawerFooter>
              <CancelButton variant="outline" mr={3} onClick={onClose}>
                Cancel
              </CancelButton>
              <Button colorScheme="whiteAlpha" type="submit">
                Send
              </Button>
            </DrawerFooter>
          </form>
        </DrawerContent>
      </StyledDrawer>
    </>
  );
};

const NavBar = styled.nav`
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
`;

const Logo = styled.div`
  text-align: right;
  span {
    font-weight: 900;
  }
`;
const Middle = styled.div`
  flex: 1;
  text-align: center;
  display: flex;
  justify-content: center;

  span {
    margin: 0 15px;
  }
`;

const ContactBtn = styled(Button)`
  margin-left: auto;
`;
const Wrapper = styled.div`
  flex-basis: 200px;
  text-align: center;
`;

const StyledDrawer = styled(Drawer)`
  background: black !important;
  color: red !important;
`;

const CancelButton = styled(Button)`
  &:hover {
    color: black !important;
  }
`;
export default Navbar;
