import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import {
  FormControl,
  FormLabel,
  Input,
  Flex,
  Button,
  Textarea,
  Skeleton,
  useToast,
  Stack,
} from '@chakra-ui/react';
import { useParams } from 'react-router-dom';
import 'firebase/firestore';
import {
  useFirestoreDocData,
  useFirestore,
  useStorage,
  useStorageDownloadURL,
} from 'reactfire';
import Dropzone from './Dropzone';
import { useHistory } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { selectAdmin } from '../features/authSlice';

const Edit = () => {
  const history = useHistory();

  useEffect(() => {
    if (
      localStorage.getItem('username') !== process.env.REACT_APP_USERNAME ||
      localStorage.getItem('password') !== process.env.REACT_APP_PASSWORD
    ) {
      history.push('/login/');
    }
  }, []);

  const [showLinks, setShowLinks] = useState(false);
  const [ShowImages, setShowImages] = useState(false);
  const [Github, setGithub] = useState('');
  const [Webpage, setWebpage] = useState('');
  const [TitleText, setTitleText] = useState('');
  const [DescriptionText, setDescriptionText] = useState('');
  const { pageId } = useParams();

  const toast = useToast();
  // Firestore Data

  const dataRef = useFirestore().collection('data').doc(pageId);
  const arrayDataRef = useFirestore().collection('projects').doc('1');

  const { status, data } = useFirestoreDocData(dataRef);
  const arrayData = useFirestoreDocData(arrayDataRef);

  useEffect(() => {
    if (data) {
      setTitleText(data.title);
      setDescriptionText(data.description);
      setWebpage(data.website);
      setGithub(data.github);
    }
  }, [data]);

  const saveContent = () => {
    dataRef.set({
      title: TitleText ? TitleText : '',
      description: DescriptionText ? DescriptionText : '',
      github: Github ? Github : '',
      website: Webpage ? Webpage : '',
    });

    // New Array
    var newArrayDataRef = arrayData.data.projectData.map(
      individualArrayData => {
        // console.log(individualArrayData.id + ' ' + pageId);
        if (individualArrayData.id == pageId) {
          individualArrayData.name = TitleText;
          // console.log(individualArrayData.name);
        }
        return individualArrayData;
      }
    );
    // console.log(newArrayDataRef);
    arrayDataRef.set({
      projectData: newArrayDataRef,
    });

    toast({
      title: 'Content has saved successfully',
      status: 'success',
    });
  };

  return (
    <Container>
      <h1> {pageId}</h1>
      <Wrapper>
        <Flex align="center">
          <Title> Edit Content </Title>
          <Button colorScheme="green" ml="auto" onClick={() => saveContent()}>
            Save Content
          </Button>
        </Flex>
        <FormControl>
          <Button
            mt={5}
            size="sm"
            colorScheme="orange"
            onClick={() => setShowLinks(!showLinks)}
          >
            {showLinks ? <span> Hide Links</span> : <span> Show Links</span>}
          </Button>
          <Button
            mt={5}
            ml={5}
            size="sm"
            colorScheme="blue"
            onClick={() => setShowImages(!ShowImages)}
          >
            {ShowImages ? <span> Hide Images</span> : <span> Show Images</span>}
          </Button>
          <FormLabel mt={5}> Project Title</FormLabel>
          {status == 'loading' ? (
            <Skeleton height="20px" />
          ) : (
            <Input
              value={TitleText}
              onChange={event => setTitleText(event.target.value)}
              type="text"
              mb={5}
            />
          )}

          {ShowImages && <Dropzone pageId={pageId} />}

          {showLinks && (
            <LinkWrapper>
              <FormLabel> Github code link</FormLabel>
              <Input
                value={Github}
                onChange={event => setGithub(event.target.value)}
                type="text"
              />
              <FormLabel mt={5}> Website link </FormLabel>
              <Input
                value={Webpage}
                onChange={event => setWebpage(event.target.value)}
                type="text"
              />
            </LinkWrapper>
          )}
          <FormLabel mt={5}> Description </FormLabel>
          {status == 'loading' ? (
            <Stack>
              <Skeleton height="20px" />
              <Skeleton height="20px" />
              <Skeleton height="20px" />
            </Stack>
          ) : (
            <Textarea
              value={DescriptionText}
              onChange={event => setDescriptionText(event.target.value)}
              h="500px"
            />
          )}
        </FormControl>
      </Wrapper>
    </Container>
  );
};

const Container = styled.div`
  min-height: 100vh;
  background: white;
  color: black;
`;

const Wrapper = styled.div`
  width: 80%;
  margin: auto;
`;
const Title = styled.h1`
  padding-top: 30px;
  font-weight: bold;
  font-size: 35px;
`;

const LinkWrapper = styled.div`
  margin-top: 20px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  padding: 20px;
`;
export default Edit;
